body.toolbar-horizontal {
  // height: 39px
  &:not(.toolbar-loading) {
    .sticky-wrapper {
      .stuck {
        .animate;
        top: 39px !important;
      }
    }

    //   height:70px
    &.toolbar-tray-open {
      .sticky-wrapper {
        .stuck {
          top: 79px !important;
        }
      }
    }
  }
}
