/* Text */
@text-color-darker: @global-color;
@text-color-dark: @global-color-darker;
@text-color: rgba(0, 0, 0, .6); //  #212121
@header-color: rgba(0, 0, 0, .87); //  #616161
@disable-color: rgba(0, 0, 0, 0.38); //  #9e9e9e
@text-color-light: @global-color-light;
@text-color-lighter: @global-color-lighter;

/* Font size */
@font-size-larger: ceil((@font-size * 1.7));
@font-size-larg: ceil((@font-size * 1.25)); // 18px
@font-size: 14px;
@font-size-small: 13px;
@font-size-mini: ceil((@font-size * 0.85)); // 12px

/* Title */
@h1: floor((@font-size * 2.6)); // 36px
@h2: floor((@font-size * 2.15)); // 30px
@h3: ceil((@font-size * 1.7)); // 24px
@h4: ceil((@font-size * 1.25)); // 18px
@h5: @font-size; // 14px
@h6: ceil((@font-size * 0.85)); // 12px

/* Font family */
@font-family: "Segoe UI", "Lucida Grande", Helvetica, "Microsoft YaHei", "", FreeSans, "Hiragino Sans GB", "Hiragino Sans GB W3", Arial, sans-serif;
@font-family-title: "Microsoft YaHei", "", 'Montserrat', "Helvetica", "Lucida Grande", "Lucida Sans", STXihei, "", sans-serif;
