// Grid
@import (less) "../../../bower_components/bootstrap/less/mixins/grid-framework.less";
@import (less) "../../../bower_components/bootstrap/less/mixins/grid.less";
@import (less) "../../../bower_components/bootstrap/less/grid.less";

// Grid system
// --------------------------------------------------

//** Number of columns in the grid.
@grid-columns: 12;
//** Padding between columns. Gets divided in half for the left and right.
@grid-gutter-width: @space;
// Navbar collapse
//** Point at which the navbar becomes uncollapsed.
@grid-float-breakpoint: @screen-sm-min;
//** Point at which the navbar begins collapsing.
@grid-float-breakpoint-max: (@grid-float-breakpoint - 1);

//== Container sizes
//
//## Define the maximum width of `.container` for different screen sizes.
// .make-xs-column(num);
// Small screen / tablet
@container-tablet: (720px + @grid-gutter-width);
//** For `@screen-sm-min` and up.
@container-sm: @container-tablet;

// Medium screen / desktop
@container-desktop: (940px + @grid-gutter-width);
//** For `@screen-md-min` and up.
@container-md: @container-desktop;

// Large screen / wide desktop
@container-large-desktop: (1140px + @grid-gutter-width);
//** For `@screen-lg-min` and up.
@container-lg: @container-large-desktop;

.row {
  div {
    .g-in;
  }

  .col-sm-10-2 {
    float: left;
    .inside-min-lr;
  }
}

@media (min-width: @screen-sm-min) {
  .col-sm-10-2 {
    width: 20%;
  }

  .sm-t-c {
    text-align: center;
  }
}

@media (min-width: @screen-md-min) {
  .col-md-10-2 {
    width: 20%;
  }
}

@media (min-width: @screen-lg-min) {
  .col-lg-10-2 {
    width: 20%;
  }
}

@media (max-width: @screen-xs-max) {
  .hidden-xs {
    display: none !important;
  }

  .col-xs-10-2 {
    width: 20%;
  }
}

@media (min-width: @screen-sm-min) and (max-width: @screen-sm-max) {
  .hidden-sm {
    display: none !important;
  }

  .col-sm-10-2 {
    width: 20%;
  }
}

@media (min-width: @screen-md-min) and (max-width: @screen-md-max) {
  .hidden-md {
    display: none !important;
  }

  .col-md-10-2 {
    width: 20%;
  }
}

@media (min-width: @screen-lg-min) {
  .hidden-lg {
    display: none !important;
  }

  .col-lg-10-2 {
    width: 20%;
  }
}
