// font-face define {theme}/css/icon.css

.icon{
  position: relative;
  display: inline-block;
  font-family: "davyin";
  font-style: normal;
  font-weight: normal;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  -webkit-text-stroke-width: 0.2px;
  -moz-osx-font-smoothing: grayscale;
}
.icon-2x{
  font-size: 200%;
}
.icon-2x-h{
  line-height: 200%;
  min-height: 200%;
}
.icon-3x{
  font-size: 300%;
}
.icon-3x-h{
  line-height: 300%;
  min-height: 300%;
}
.icon-4x{
  font-size: 400%;
}
.icon-4x-h{
  line-height: 400%;
  min-height: 400%;
}
